<template>
  <div class="post-wrap">
    <h3>開發動機</h3>
    <p>因為我想要得到我想要的音樂的簡譜，但是自己無法聽音扒譜，所以要去求助能夠聽音扒譜的人類。 但是能夠聽音扒譜的人類覺得手寫或打字都太麻煩了，為了讓他只要用滑鼠按一按就能輕鬆產生簡譜，而製造的懶人專屬工具。</p>

    <h3>操作示範（螢幕錄影）</h3>
    <!-- Video -->
    <div class="device-wrap video-container">
        <iframe width="560" height="315" src="https://www.youtube.com/embed/B5xFbR7eTlI" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>
    </div>

    <h3>功能介紹</h3>
    <h4>樂譜面板：顯示音符的地方。</h4>
    <ul>
        <li>輸入歌名：雙擊歌名區塊可以輸入或修改歌名。</li>
        <li>修改軌道顏色：雙擊軌道區塊可以修改軌道左側的邊線顏色（白、黃、綠、紫、橘）。</li>
        <li>刪除指定軌道：點擊軌道右上方的叉叉按鈕，可以刪除該軌道及內容。</li>
        <li>播放指定軌道：點擊軌道右下方的播放按鈕，可以播放該軌道的音檔。</li>
        <li>
            移動「焦點」：點擊軌道或者音符可移動焦點，成為焦點的元素周圍會有發光造型。
            <ul>
                <li>軌道焦點（鍵盤快捷鍵 上：<span class="inline-wrap">W</span>，下：<span class="inline-wrap">S</span>）：當軌道獲得焦點，軌道中的最後一個音符也會獲得焦點。</li>
                <li>音符焦點（鍵盤快捷鍵 左：<span class="inline-wrap">A</span>，右：<span class="inline-wrap">D</span>）：當音符獲得焦點，音符所在軌道也會獲得焦點。</li>
            </ul>
        </li>
    </ul>

    <h4>鋼琴面板：初始位置設定在最常用的高音部分。</h4>
    <ul>
        <li>新增/插入音符：按下鋼琴面板中的琴鍵，即可於畫面上的「焦點音符後方」產生對應的音符。</li>
        <li>播放音檔：按下鋼琴面板中的琴鍵，會播放對應的琴鍵音檔。</li>
    </ul>

    <h4>工具箱面板：右側工具箱（預設：開啟）。</h4>

    <h5>音符</h5>
    <ul>
        <li>刪除音符（鍵盤快捷鍵 <span class="inline-wrap">Backspace</span>）：刪除一個「焦點音符」。</li>
        <li>空格音符（鍵盤快捷鍵 <span class="inline-wrap">Space</span>）：增加一個空心的音符於「焦點音符後方」。</li>
    </ul>

    <h5>軌道</h5>
    <ul>
        <li>新增一行（鍵盤快捷鍵 <span class="inline-wrap">Enter</span>）：增加一行空白軌道。</li>
        <li>複製此行：複製「焦點軌道和其音符」於其後方。</li>
        <li>斷開此行：將「焦點軌道」中，以「焦點音符」的索引為基準，斷開成新的兩行軌道。第一行為開頭至焦點前，第二行為焦點至結尾。</li>
        <li>上移此行：將「焦點軌道」往上移動。</li>
        <li>下移此行：將「焦點軌道」往下移動。</li>
    </ul>

    <h5>面板控制</h5>
    <ul>
        <li>關閉/開啟鋼琴：控制鋼琴面板是否顯示於畫面（預設：開啟）</li>
        <li>切換模式：切換樂譜面板的顯示模式，有簡譜及五線譜（預設：簡譜模式）。</li>
    </ul>

    <h5>輸出：使用 html2canvas 開發此功能。</h5>
    <ul>
        <li>輸出圖片：將完成的作品輸出成一張圖片預覽在畫面上，右鍵可另存圖檔。</li>
    </ul>

    <h5>切換語系：使用 Vue.js 套件開發此功能。</h5>
    <ul>
        <li>
            提供兩種語系介面
            <ul>
                <li>繁體中文（預設）</li>
                <li>英文</li>
            </ul>
        </li>
    </ul>
  </div>
</template>

<script>
// import jquery
import $ from "jquery";
window.$ = $;

export default {
    name: 'SpPianoNotationConversionTool',
    mounted() {
      /* if not mac, hide scroll */
      this.isMac = navigator.platform.toUpperCase().indexOf('MAC') >= 0; // not found = -1
      let $dev_outter_wrap = $('.dev-outter-wrap');
      if ( !this.isMac && $dev_outter_wrap.length > 0 ) {
        $dev_outter_wrap.addClass('hide-scroll');
      }

      /* tabs feature */
      $('.tab-nav').on('click', function(){
        let tab_panel = $(this).parent('.tab-nav-wrap').attr('data-panel');
        
        let tab = $(this).attr('data-tabs');
        $(this).siblings('.active').removeClass('active');
        $(this).addClass('active');
    
        let $tab_content_wrap = $('.tab-content-wrap').filter(`[data-panel="${tab_panel}"]`);
        $tab_content_wrap.find('.active').removeClass('active');
        $tab_content_wrap.find(`[data-tabs="${tab}"]`).addClass('active');

        $('html, body').animate({
            'scrollTop': $(this).offset().top - 70
        }, 300);
      });
    }
}
</script>

